import React, {DetailedHTMLProps, InputHTMLAttributes, ReactNode, useState} from 'react'
import SuperInputText from '../../../hw04/common/c1-SuperInputText/SuperInputText'

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
export type SuperDebouncedInputPropsType = Omit<DefaultInputPropsType, 'type'> & {
    //  +       
    onChangeText?: (value: string) => void
    onEnter?: () => void
    error?: ReactNode
    spanClassName?: string
} //    SuperInputTextPropsType
    & { //    SuperPagination
    onDebouncedChange?: (value: string) => void
}

const SuperDebouncedInput: React.FC<SuperDebouncedInputPropsType> = (
    {
        onChangeText,
        onDebouncedChange,

        ...restProps //       restProps
    }
) => {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)

    const onChangeTextCallback = (value: string) => {
        onChangeText?.(value)
        console.log(value)
        if (onDebouncedChange) {
            //  
            clearTimeout(timerId)
            //   
            //    1500ms,    
            const ID = setTimeout(() => onDebouncedChange(value), 1500);
            //
            setTimerId(+ID)
        }
    }

    return (
        <SuperInputText onChangeText={onChangeTextCallback} {...restProps} style={{
            width: '468px',
            fontStyle: 'normal',
            fontWeight: 400,
            fontSize: '18px',
            lineHeight: '21px'
        }} />
    )
}

export default SuperDebouncedInput
