import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        setTimerId(+setInterval(() => setDate(new Date()), 1000))
        // setInterval(() => setDate(new Date()), 1000);

    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        clearInterval(timerId)
        setTimerId(undefined)
    }

    const onMouseEnter = (/*event: MouseEvent<HTMLDivElement, MouseEvent>*/) => { //   //     
        // return  event.name === 'mouseover' ? setShow(true) : '';
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const stringTime = date.toTimeString().slice(0, 8) || <br/> //'date->time'   .toTimeString().slice(0,8) || <br/>  // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = `${+date.toLocaleString("en-US", {day: 'numeric'}) < 10 ? `0` + date.toLocaleString("en-US", {day: 'numeric'}) : date.toLocaleString("en-US", {day: 'numeric'})}.${date.getMonth() + 1}.${date.getFullYear()}` ||
        <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = new Intl.DateTimeFormat('en-US', {weekday: 'long'}).format(date)/*date.getDay()*/ || <br/> //  
    const stringMonth = date.toLocaleString("en-US", {month: 'long'}) /*date.getMonth()+1*/ || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    id={'hw9-button-start'}
                    disabled={!!timerId} // true   //    
                    onClick={start}
                >
                    start
                </SuperButton>
                <SuperButton
                    id={'hw9-button-stop'}
                    disabled={!timerId} // true   //     
                    onClick={stop}
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
